import "vant/es/tabs/style/less";
import _Tabs from "vant/es/tabs";
import "vant/es/tab/style/less";
import _Tab from "vant/es/tab";
import "vant/es/pull-refresh/style/less";
import _PullRefresh from "vant/es/pull-refresh";
import "vant/es/list/style/less";
import _List from "vant/es/list";
import { computed, defineComponent, onMounted, ref } from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
import CustomizeTab from '@/components/customizeTab.vue';
import { useRouteParams } from '@/hooks/useRouteParams';
import CustomizeWrapper from '@/components/customizeWrapper.vue';
import dayjs from 'dayjs';
export default defineComponent({
  components: {
    [_List.name]: _List,
    [_PullRefresh.name]: _PullRefresh,
    [_Tab.name]: _Tab,
    [_Tabs.name]: _Tabs,
    CustomizeTab,
    CustomizeWrapper
  },

  setup() {
    const store = useStore();
    const active = ref('0');
    const route = useRoute();
    onMounted(() => onRefresh());
    const {
      creditUserId: payUserId
    } = useRouteParams();

    const onLoad = () => {
      let params = {
        data: {
          payUserId,
          orderType: active.value
        }
      };
      store.dispatch('orderModule/loadOrder', params);
    };

    const onRefresh = () => {
      const {
        params: {
          creditUserId: payUserId
        }
      } = route;
      let params = {
        data: {
          payUserId,
          orderType: active.value
        }
      };
      store.dispatch('orderModule/fetchOrder', params);
    };

    const loading = computed({
      get() {
        return store.state.orderModule.loading;
      },

      set(v) {
        store.state.orderModule.loading = v;
      }

    });
    const refreshing = computed({
      get() {
        return store.state.orderModule.refreshing;
      },

      set(v) {
        store.state.orderModule.refreshing = v;
      }

    });

    const tabClick = index => {
      window.scrollTo(0, 0);
      active.value = index;
      let params = {
        data: {
          payUserId,
          orderType: index
        }
      };
      store.dispatch('orderModule/loadOrder', params);
    };

    const formateOrderType = v => {
      return v === '未完成' ? '已记账' : v;
    };

    const formateStr = v => {
      return dayjs(v).format('YYYY-MM-DD');
    };

    return {
      formateStr,
      order: computed(() => store.state.orderModule.order),
      onLoad,
      loading,
      finished: computed(() => store.state.orderModule.finished),
      refreshing,
      onRefresh,
      tabClick,
      formateOrderType,
      active
    };
  }

});